import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper';
import img from '../../../img/schedule/tventymatch.jpg';
import img2 from '../../../img/schedule/sparring.jpg';
import img3 from '../../../img/schedule/sparring2.jpg';
import { ImgLoading } from '../../../components';

import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

export default function ScheduleSlider() {
  return (
    <Swiper
      loop={true}
      className='swiper-schedule'
      spaceBetween={0}
      slidesPerView={1}
      autoplay={{
        delay: 2000,
        disableOnInteraction: false,
      }}
      pagination={{
        dynamicBullets: true,
      }}
      modules={[Pagination, Autoplay]}
    >
      <SwiperSlide>
        <ImgLoading
          img={`${process.env.PUBLIC_URL}${img}`}
          name='schedulebanner__back'
        />
      </SwiperSlide>
      <SwiperSlide>
        <ImgLoading
          img={`${process.env.PUBLIC_URL}${img2}`}
          name='schedulebanner__back'
        />
      </SwiperSlide>
      <SwiperSlide>
        <ImgLoading
          img={`${process.env.PUBLIC_URL}${img3}`}
          name='schedulebanner__back'
        />
      </SwiperSlide>
    </Swiper>
  );
}
